import React, { useContext } from 'react';
import {
  View,
  Text,
  TextInput,
  TouchableOpacity,
  Platform,
  TextInputKeyPressEventData,
} from 'react-native';
import { NavigationProp } from '@react-navigation/native';
import { shadow } from 'screens/PasswordGenerator/styles';
import Icon from 'components/SVGs/Icon';
import styles from 'screens/Login/styles';
import { appColors } from 'utils/constants';
import { I18n } from 'i18n-js';
import { TranslationsContext } from '../../contexts/translationsContext';
import loginSignupTranslations from '../../languages/loginSignupTranslations';

const webStyles: string =
  Platform.OS === 'web'
    ? `
    self-center
    w-full
    sm:w-11/12
    md:w-1/2
    lg:w-5/12
    xl:w-[28%]`
    : '';

export const SignUp = ({ navigation, username, handlers }: Props) => {
  const { loginSignupTranslations } = useContext(TranslationsContext);
  const { handleSignUp, handleOnChangeText, handleOnKeyPress } = handlers;

  return (
    <View className={webStyles} style={styles.container}>
      <Icon
        name='PassvaultLogoIcon'
        width={130}
        height={130}
        variant={{ fillColor: appColors.primary }}
      />
      <View style={styles.inputContainer}>
        <TextInput
          placeholder={loginSignupTranslations.t('usernamePlaceholder')}
          value={username}
          style={[styles.input, shadow.container]}
          onChangeText={handleOnChangeText}
          onKeyPress={handleOnKeyPress}
        />
      </View>

      <View style={styles.buttonContainer}>
        <TouchableOpacity onPress={handleSignUp} style={styles.button}>
          <Text style={styles.buttonText}>{loginSignupTranslations.t('signupButton')}</Text>
        </TouchableOpacity>
      </View>

      <View style={styles.linkContainer}>
        <TouchableOpacity onPress={() => navigation.navigate('Login')} style={styles.link}>
          <Text style={styles.linkText}>{loginSignupTranslations.t('goBack2Login')}</Text>
        </TouchableOpacity>
      </View>
    </View>
  );
};

export interface Props {
  navigation: NavigationProp<any>;
  username: string;
  handlers: {
    handleSignUp: () => void;
    handleOnChangeText: (text: string) => void;
    handleOnKeyPress: ({ nativeEvent }: { nativeEvent: TextInputKeyPressEventData }) => void;
  };
}
