import React, { useContext } from 'react';
import {
  Text,
  TextInput,
  View,
  TouchableOpacity,
  Platform,
  TextInputKeyPressEventData,
} from 'react-native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { LoadingIndicator } from 'components/LoadingIndicator/loadingIndicator';
import { CustomPopover } from 'components/Popover/popover';
import { CustomSnackbar } from 'components/CustomSnackbar/customSnackbar';
import { appColors, popoverMessage } from 'utils/constants';
import { shadow } from 'screens/PasswordGenerator/styles';
import Icon from 'components/SVGs/Icon';
import { I18n } from 'i18n-js';
import styles from './styles';
import { TranslationsContext } from '../../contexts/translationsContext';
import loginSignupTranslations from '../../languages/loginSignupTranslations';

const webStyles: string =
  Platform.OS === 'web'
    ? `
    self-center
    w-full
    sm:w-11/12
    md:w-1/2
    lg:w-5/12
    xl:w-[28%]`
    : '';

export const Login = ({
  navigation,
  username,
  isRequesting,
  snackbarVisible,
  touchableRef,
  handlers,
}: Props) => {
  const { loginSignupTranslations } = useContext(TranslationsContext);
  const { handleOnChangeText, handleLogin, handleOnKeyPress, handleOnDismiss } = handlers;

  if (isRequesting) return <LoadingIndicator />;

  return (
    <View className={webStyles} style={styles.container}>
      <Icon
        name='PassvaultLogoIcon'
        width={130}
        height={130}
        variant={{ fillColor: appColors.primary }}
      />
      <View style={styles.inputContainer}>
        <TextInput
          placeholder={loginSignupTranslations.t('usernamePlaceholder')}
          value={username}
          style={[styles.input, shadow.container]}
          onChangeText={handleOnChangeText}
          onKeyPress={handleOnKeyPress}
        />
      </View>

      <View style={styles.buttonContainer}>
        <TouchableOpacity onPress={handleLogin} style={styles.button}>
          <Text style={styles.buttonText}>{loginSignupTranslations.t('loginButton')}</Text>
        </TouchableOpacity>

        <TouchableOpacity
          onPress={() => {
            navigation.navigate('SignUp');
          }}
          style={[styles.button, styles.buttonOutLine]}
        >
          <Text style={styles.buttonOutLineText}>{loginSignupTranslations.t('signupButton')}</Text>
        </TouchableOpacity>
      </View>

      <View style={styles.linkContainer}>
        <TouchableOpacity
          onPress={() => {
            navigation.navigate('PasswordGenerator');
          }}
          style={styles.link}
        >
          <Text style={styles.linkText}>{loginSignupTranslations.t('tooltip')} </Text>
        </TouchableOpacity>

        <CustomPopover touchableRef={touchableRef} />
      </View>

      <CustomSnackbar
        message={popoverMessage}
        isSnackbarVisible={snackbarVisible}
        onDismiss={handleOnDismiss}
      />
    </View>
  );
};

export interface Props {
  navigation: DrawerNavigationProp<any>;
  username: string;
  isRequesting: boolean;
  snackbarVisible: boolean;
  touchableRef: any;
  handlers: {
    handleOnChangeText: (text: string) => void;
    handleLogin: () => void;
    handleOnKeyPress: ({ nativeEvent }: { nativeEvent: TextInputKeyPressEventData }) => void;
    handleOnDismiss: () => void;
  };
}
