import React, { useContext } from 'react';
import { Text, ScrollView, ImageBackground, View } from 'react-native';
import { DrawerItemList, DrawerItem } from '@react-navigation/drawer';
import {
  DrawerDescriptorMap,
  DrawerNavigationHelpers,
} from '@react-navigation/drawer/lib/typescript/src/types';
import { DrawerNavigationState, ParamListBase } from '@react-navigation/routers';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import { MaterialCommunityIcons, MaterialIcons, FontAwesome } from '@expo/vector-icons';
import { Drawer } from 'react-native-paper';
import { imageBackground } from './styles';
import { expo } from '../../../app.json';
import { TranslationsContext } from 'contexts/translationsContext';

export const CustomSidebar = ({ sidebarProps, userName, handlers }: Props) => {
  const { sidebarTranslations } = useContext(TranslationsContext);
  const { handleSignOut, onPressInPasswordGenerator, onPressInCheckTutorial } = handlers;

  return (
    <SafeAreaProvider>
      <ImageBackground
        source={require('assets/img/sidebar-banner.png')}
        style={imageBackground.container}
      >
        <Text style={imageBackground.name}>{`${sidebarTranslations.t(
          'welcome'
        )}, ${userName} !`}</Text>
        <Text className='rounded-md mt-2 px-2 py-1 bg-pblue-400/[.7] self-end text-[10px] font-bold text-white'>
          Passvault v{expo.version}
        </Text>
      </ImageBackground>
      <View className='flex flex-grow justify-between'>
        <Drawer.Section title={sidebarTranslations.t('pages')}>
          <ScrollView style={{ paddingTop: 10 }}>
            <DrawerItemList {...sidebarProps} />
            <DrawerItem
              label={sidebarTranslations.t('passwordGenerator')}
              icon={({ color, size }) => (
                <MaterialCommunityIcons name='engine-outline' size={size * 0.75} color={color} />
              )}
              onPress={() => onPressInPasswordGenerator(sidebarProps.navigation)}
            />
            <DrawerItem
              label={sidebarTranslations.t('checkTutorial')}
              icon={({ color, size }) => (
                <FontAwesome name='graduation-cap' size={size * 0.6} color={color} />
              )}
              onPress={() => onPressInCheckTutorial(sidebarProps.navigation)}
            />
          </ScrollView>
        </Drawer.Section>
        <DrawerItem
          icon={({ color, size }) => (
            <MaterialIcons name='logout' size={size * 0.75} color={color} />
          )}
          label={sidebarTranslations.t('signOut')}
          onPress={handleSignOut}
        />
      </View>
    </SafeAreaProvider>
  );
};

export interface Props {
  sidebarProps: {
    state: DrawerNavigationState<ParamListBase>;
    navigation: DrawerNavigationHelpers;
    descriptors: DrawerDescriptorMap;
  };
  userName: string;
  handlers: {
    handleSignOut: () => void;
    // eslint-disable-next-line no-unused-vars
    onPressInPasswordGenerator: (navigation: DrawerNavigationHelpers) => void;
    // eslint-disable-next-line no-unused-vars
    onPressInCheckTutorial: (navigation: DrawerNavigationHelpers) => void;
  };
}
