import React, { useContext } from 'react';
import {
  View,
  ScrollView,
  Platform,
  Text,
  TextInput,
  TouchableOpacity,
  NativeSyntheticEvent,
  NativeScrollEvent,
} from 'react-native';
import { DrawerNavigationProp } from '@react-navigation/drawer';
import { EvilIcons, Entypo } from '@expo/vector-icons';
import { FAB } from 'react-native-paper';
import { PasswordItem } from 'components/PasswordItem/passwordItem';
import { LoadingIndicator } from 'components/LoadingIndicator/loadingIndicator';
import { CustomSnackbar } from 'components/CustomSnackbar/customSnackbar';
import { CustomModal as Modal } from 'components/CustomModal/customModal';
import { appColors, PasswordI } from 'utils/constants';
import styles from './styles';
import { TranslationsContext } from 'contexts/translationsContext';

const webStyles: string =
  Platform.OS === 'web'
    ? `
    self-center
    w-full
    sm:border-l-2
    sm:border-r-2
    sm:border-[#3091e0]
    sm:w-11/12
    md:w-1/2
    lg:w-5/12
    xl:w-[28%]`
    : '';

export const PasswordList = ({
  navigation,
  snackbarObj,
  modalObj,
  scrollIsClose2Bottom,
  hasPasswordsStored,
  passwordsFiltered,
  searchText,
  isRequesting,
  handlers,
}: Props) => {
  const { passwordListTranslations, modalTranslations } = useContext(TranslationsContext);
  const { snackbarVisible, snackbarMessage } = snackbarObj;
  const { modalTitle, modalMessage, modalVisible } = modalObj;
  const {
    handleScrollIsClose2Bottom,
    handleOnDismissSnackbar,
    handleOnPressFAB,
    handleModalToggle,
    handleModalOnSubmit,
    handleSearch,
    handleClearSearch,
  } = handlers;

  if (isRequesting) return <LoadingIndicator />;

  return (
    <View className={webStyles} style={styles.container}>
      {hasPasswordsStored && (
        <View className='flex flex-row justify-between items-center m-6 px-4 bg-white rounded-3xl'>
          <View className='w-5/6 md:w-11/12 flex flex-row items-center'>
            <EvilIcons name='search' size={24} color={appColors.disabledColorSlider} />
            <TextInput
              className={`w-full ${Platform.OS === 'web' ? 'p-3' : 'p-2'} outline-none`}
              style={styles.searchInput}
              value={searchText}
              placeholder={passwordListTranslations.t('searchInputPlaceholder')}
              onChangeText={handleSearch}
            />
          </View>
          <TouchableOpacity onPress={handleClearSearch}>
            <Entypo name='circle-with-cross' size={20} color={appColors.disabledColorSlider} />
          </TouchableOpacity>
        </View>
      )}
      {passwordsFiltered.length > 0 ? (
        <ScrollView
          scrollEventThrottle={16}
          onScroll={handleScrollIsClose2Bottom}
          contentContainerStyle={{ flexGrow: 1 }}
          keyboardShouldPersistTaps='handled'
          fadingEdgeLength={150}
        >
          <View style={[styles.items, { marginTop: passwordsFiltered.length > 4 ? 0 : 20 }]}>
            {passwordsFiltered.map(
              ({
                id,
                description,
                passwordGenerated,
                socialMedia,
                pwdWasCorrectlyEncrypted,
              }: PasswordI) => (
                <PasswordItem
                  key={id}
                  passwordId={id}
                  passwordGenerated={passwordGenerated}
                  socialMedia={socialMedia}
                  description={description}
                  pwdWasCorrectlyEncrypted={pwdWasCorrectlyEncrypted}
                  navigation={navigation}
                />
              )
            )}
          </View>
        </ScrollView>
      ) : (
        <View className='flex flex-1 items-center justify-center'>
          <Text className='text-md'>{passwordListTranslations.t('noPasswords')}</Text>
        </View>
      )}
      <FAB
        style={[styles.fab, { bottom: snackbarVisible ? 40 : 0 }]}
        color={appColors.textTint}
        visible={!scrollIsClose2Bottom}
        icon='plus'
        onPress={handleOnPressFAB}
      />
      <CustomSnackbar
        message={snackbarMessage}
        isSnackbarVisible={snackbarVisible}
        onDismiss={handleOnDismissSnackbar}
      />
      <Modal
        visible={modalVisible}
        toggle={handleModalToggle}
        onSubmit={handleModalOnSubmit}
        title={modalTitle}
        message={modalMessage}
        noLabel={modalTranslations?.t('noLabel')}
        yesLabel={modalTranslations?.t('yesLabel')}
      />
    </View>
  );
};

export interface Props {
  navigation: DrawerNavigationProp<any>;
  snackbarObj: {
    snackbarVisible: boolean;
    snackbarMessage: string;
  };
  modalObj: {
    modalTitle: string;
    modalMessage: string;
    modalVisible: boolean;
  };
  scrollIsClose2Bottom: boolean;
  hasPasswordsStored: boolean;
  passwordsFiltered: Array<PasswordI>;
  searchText: string;
  isRequesting: boolean;
  handlers: {
    handleScrollIsClose2Bottom: (event: NativeSyntheticEvent<NativeScrollEvent>) => void;
    handleOnDismissSnackbar: () => void;
    handleOnPressFAB: () => void;
    handleModalToggle: () => void;
    handleModalOnSubmit: () => void;
    handleSearch: (text: string) => void;
    handleClearSearch: () => void;
  };
}
